.login-page{
    max-width: 500px ;
    border: 2px solid rgb(3, 165, 206) ;
    border-radius: 5px ;
    padding: 30px ;
    margin: 50px auto ;
}
.login-page h2{
    text-transform: uppercase ;
    letter-spacing: 2px ;
    color: #555 ;
    text-align: center;
    padding-bottom: 20px;
}
.login-page form input,
.login-page form button{
    width: 100% ;
    height: 40px ;
    margin: 10px 0 ;
    padding: 0 5px ;
    outline: rgb(3, 165, 206) ;
    border: 1px solid rgb(3, 165, 206) ;
}
.login-page form .row{
    display: flex ;
    justify-content: space-between ;
    align-items: center ;
}
.login-page form button{
    width: 150px ;
    background: #2196f3 ;
    color: white ;
    text-transform: uppercase ;
    letter-spacing: 2px ;
    cursor: pointer ;
    margin-left: 11rem;
}
.login-page form a{
    color: orange ;
    letter-spacing: 1.3px ;
    text-transform: uppercase ;
}